                  <div class="">
                      <div class="bg-white shadow rounded-t-lg" style="width:150px;">
                        <span class="ml-3 p-2 font-bold text-sm hover:text-white cursor-pointer hover:bg-green-400">VTour RealEstate</span>
                        <!-- <span class="ml-3 p-2 font-bold text-lg hover:text-white cursor-pointer hover:bg-green-400">Buy</span> -->
                      </div>
                      <div class="bg-white shadow rounded-b-lg p-3" >
                          <div class="row justify-content-center">
                            <div class="col-4">
                                <div class="sm-form ">
                                    <p class="text-lg font-semibold">Pencarian</p>
                                    <input type="text" id="pencarian" name="pencarian" class="form-control form-control-sm" placeholder="pencarian" v-model="properti.pencarian" >
                                </div>
                            </div>
