                                <p class="text-lg font-semibold">Type</p>
                                <select class='form-control' v-model="properti.type">
                                    <option value="">----</option>
                                    <option>Real Estate</option>
                                    <option>Wisata</option>
                                </select>   
                            </div>
                            <div class="col-4">
                                <!-- <button type="button" class="btn btn-sm btn-success float-right "><span class="typcn typcn-zoom"></span> Search </button> -->
                                <p class="text-lg font-semibold">Lokasi</p>
                                <div class="sm-form ">
                                    <input type="text" id="harga" name="harga" class="form-control form-control-sm" placeholder="Lokasi" v-model="properti.lokasi" >
                                </div>
                            </div>
                          </div>
                      </div>
                  </div>    
                  <div class=" p-3 mt-3 shadow rounded">
                      <div class="row justify-content-center">
                            <div class="col-sm-4 col-md-3 cursor-pointer"
                            @click="preview360(item)"
                            v-for="(item, index) in td" :key="index+'tem'">
                                <div class="p-3">
                                    <div class="text-center">
                                    <img :src="item.gambar"
                                    style="height:150px;width:100%;display:block;margin:auto;"
                                    alt="">
                                    </div>
