                                        {{item.kategori}}
                                    </p>
                                    <div>
                                        <p class="text-green-600 font-bold text-lg">{{item.deskripsi}}</p>
                                    </div>
                                    <div class="text-xs text-white font-semibold">
                                        <span class="text-xs" style="font-size:9px;">{{item.tag}}</span>
                                        <br>
                                    </div>
                                </div>
                            </div>
                        </div>
                  </div>
              </div>
            </div>
        </div>
    </div>
</template>
<script>
import autophp from '@/plugins/autophp';
let sdb = new autophp();
import Preview from '@/components/estate/Preview.vue'
export default{
    components:{
        Preview
    },
    computed:{
        td(){
            let that=this;
            let data = this.datanya
            data.forEach(e=>{
                e.harga=parseInt(e.harga)
            })
            data=data.filter(e=>{
                if(e.type.toLowerCase().indexOf(that.properti.type.toLowerCase())!=-1){
                    return e;
                }
            })
            data=data.filter(e=>{
                if(e.kategori.toLowerCase().indexOf(that.properti.pencarian.toLowerCase())!=-1){
                    return e;
                }
            })
            //  data=data.filter(e=>{
            //     if(e.deskripsi.toLowerCase().indexOf(that.properti.lokasi.toLowerCase())!=-1){
            //         return e;
            //     }
            // })
            return data;
        }
    },
    mounted() {
        this.getData();
    },
    methods: {
         preview360(data){
             let that=this;
             sdb.collection("app_gis_360data",false).doc().select(`SELECT * FROM app_gis_360data WHERE idpeta='${data.id}' AND main='true'`).then(res => {
                that.$router.push(`office/360view?idgambar=${res[0].id}&idpeta=${data.id}`)
            });
        },
        getData(){
            sdb.collection("app_gis_360data",false).doc().select(`SELECT * FROM app_gis_kategori WHERE id IN (select idpeta from app_gis_360data) AND type='Real Estate'`).then(res => {
                this.datanya=res;
                console.log('res',res)
                this.$forceUpdate();
            });
        }
    },
    data(){
        return{
            datanya:[],
            properti:{
                pencarian:'',
                type:'',
                lokasi:'',
                harga:200000000
            },
               items:[
                   {
                       title:'Duren A',
                       gambar:'https://images.unsplash.com/photo-1582407947304-fd86f028f716?ixid=MnwxMjA3fDB8MHxzZWFyY2h8Mnx8cmVhbCUyMGVzdGF0ZXxlbnwwfHwwfHw%3D&ixlib=rb-1.2.1&w=1000&q=80',
                       harga:'100000000',
                       type:'1'
                   },
                   {
                       title:'Salak A',
                       gambar:'https://assets-us-01.kc-usercontent.com/0542d611-b6d8-4320-a4f4-35ac5cbf43a6/57134553-0077-4e93-8cfd-58895d271ef8/homeowners-insurance-facebook.jpg',
                       harga:'120000000',
                       type:'2'
                   },
                   {
