                       harga:'150000000',
                       type:'3'
                   },
                   
                   {
                       title:'Apple A',
                       gambar:'https://images.unsplash.com/photo-1582407947304-fd86f028f716?ixid=MnwxMjA3fDB8MHxzZWFyY2h8Mnx8cmVhbCUyMGVzdGF0ZXxlbnwwfHwwfHw%3D&ixlib=rb-1.2.1&w=1000&q=80',
                       harga:'140000000',
                       type:'4'
                   },
                   {
                       title:'Rambutan A',
                       gambar:'https://assets-us-01.kc-usercontent.com/0542d611-b6d8-4320-a4f4-35ac5cbf43a6/57134553-0077-4e93-8cfd-58895d271ef8/homeowners-insurance-facebook.jpg',
