               background: rgb(47,107,162);
background: linear-gradient(90deg, rgba(47,107,162,1) 0%, rgba(13,69,125,1) 100%);">
                 <button type="button" class="btn btn-sm btn-dark  float-right mr-8" @click="show3dmap" v-show="vdata.link3d">
