                           class="d-inline" style="height:10px;width:10px;" :style="`${showme?'filter: brightness(0) invert(1);':''}`" alt=""></button>
                          <span class="tipstextL">Munculkan Id Barcode</span>
                      </div>
                      <div v-if="showme?item2.icon || item2.idbarcode:item2.icon" @click="centerTo(item2)" v-for="(item2, index2) in listMarker" :key="index2+'icon'" v-show="item2.id_peta==item.id" class="hover:bg-green-400 text-xs pl-3"
                      >
                        <img :src="item2.idbarcode?'https://gis.egov.balangankab.go.id/files/me.png':item2.icon" class="d-inline" style="height:30px;width:14px;" alt=""> <img src="https://upload.egov.balangankab.go.id/svg/arrow-right.svg" 
