                    </p>
                    <div class="cursor-pointer">
                      <div
                      v-for="(item3, index3) in tdnya" :key="index3+'icon'" v-show="item3.id_peta==item.id" class="hover:bg-green-400 cursor-pointer"
                      >
                        <input type="checkbox" :id="`pilihan${index3}`"  @click="loadgeojson(item3)" v-model="layers['geojson'+item3.id+'on']" class="ml-3 cursor-pointer">
                        <label :for="`pilihan${index3}`"><img src="https://upload.egov.balangankab.go.id/svg/arrow-right.svg" 
                                class="d-inline ml-1" style="height:14px;width:14px;" alt=""> {{item3.title}}</label>
