</template>
<script>
import autophp from "@/plugins/autophp.js"; // ganti source nya
import _ from 'lodash'
import axios from 'axios'
let sdb = new autophp();
let maps;
export default{
    data(){
        return{
          photoSize:[-30.8650, 155.2094], // penting  [height, width] , more minus height = more height
          ready:false,
          mores:false,
          showMore:false,
          eventshow:false,
          showme:false,
          moreImage:[],
          eventdata:[],
          eventkey:[],
          search:'',
          kategoris:'',
          shrink:true,
          center:[-2.3615836073925434, 115.46956926581515],
          loading:true,
          show3d:false,
          listPeta:[],
          listMarker:[],
          listGeojson:[],
          geojson:[],
          nilai:[],
          geoJsonLayer:{},
          layers:{},
          vdata:{},
          markers:{},
          routeid:'5',
          listkategori:false,
        }
    },
    watch:{
     
    },
    computed:{
      tdnya(){
        let that=this;
        let data =this.listGeojson
        data=data.filter(e=>{
          if(e.title.toLowerCase().indexOf(that.search)!=-1){
            return e
          }
        })
        data=data.filter(e=>{
          if(e.kategori.toLowerCase().indexOf(that.kategoris.toLowerCase())!=-1){
            return e
          }
        })
        return data;
      }
    },
      methods: {
        showMoreImage(id){
          if(!this.showMore){
              sdb.collection("app_gis_ruangan_gambar",false).doc().select(`select * from app_gis_ruangan_gambar where idroom='${id}'`).then(res => {
                  console.log(res);
                  // console.log(`select * from app_gis_ruangan_gambar where idroom='${id}'`)
                  this.moreImage=res;
                  this.showMore=true;
                  this.$forceUpdate();
              });
          }else{
                  this.showMore=false;
          }
        },
        centerTo(item2){
          if(item2.idbarcode){
            this.$router.push(`/office?id=${this.routeid}&me=${item2.idbarcode}`)
            setTimeout(() => {
              this.$router.go();
            }, 1000);
            }else{
              maps.panTo(new L.LatLng(item2.lat,item2.lng))
          }
        },
        events(data){
          // alert(data)
        },
        bawah(e) {
          if(e.target.querySelectorAll('ul').length>0){
            e.target.querySelectorAll('ul')[0].classList.toggle('isaktif')
          }
        },
        muncul (e) {
          e.target.nextElementSibling.classList.toggle('isaktif')
        },
         parent(e){
          e.target.parentNode.click();
        },
        loadListPeta(){
          let that=this;
          sdb.collection("app_gis_kategori").doc().get("*",`where id='${this.routeid}' order by id`).then(res => {
            that.listPeta=res;
            that.$forceUpdate();
          });
          sdb.collection("app_gis_marker").doc().get("*",`where tb1.id_peta='${this.routeid}' order by id`).then(res => {
            that.listMarker=res;
            that.$forceUpdate();
          });
          sdb.collection("app_gis_geojson").doc().get("*",`where tb1.id_peta='${this.routeid}' order by id`).then(res => {
            that.listGeojson=res;
            that.$forceUpdate();
          });
         sdb.collection("app_gis_marker",false).doc().select(`select floorplan,link3d,height,width,center1,center2,nilai1,nilai2,nilai3,nilai4,idquery
          from app_gis_marker where id_peta='${that.routeid}'`).then(res => {
                // console.log(res)
                if(res.length>0){
                    that.photoSize=[parseFloat(res[0].height),parseFloat(res[0].width)]
                    // console.log(that.photoSize)
                    that.nilai[0]=parseFloat(res[0].nilai1);
                    that.nilai[1]=parseFloat(res[0].nilai2);
                    that.nilai[2]=parseFloat(res[0].nilai3);
                    that.nilai[3]=parseFloat(res[0].nilai4);
                    if(res[0].center1){
                      that.center=[parseFloat(res[0].center1),parseFloat(res[0].center2)]
                    }
                    that.vdata.floorplan=res[0].floorplan
                    that.vdata.link3d=res[0].link3d
                    that.$forceUpdate();
                }
                that.loadmap()
            });
        },
        loadmarker(item){
          // console.log(item)
          let that=this;
          let data = this.listMarker
          data=data.filter((e)=>e.id_peta==item)
           if(data.length>0){
             data.forEach((e,i)=>{
              if ( maps.hasLayer(that.markers['marker'+e.id]) ){  // if the specified layer is visible on the map, it needs to be removed
                maps.removeLayer(that.markers['marker'+e.id])
                that.markers['marker'+e.id+'on']=false
                return
              }
              that.markers['marker'+e.id+'on']=true
              // console.log('listicon',e)
              if(i==0){
                // maps.panTo(new L.LatLng(e.lat, e.lng))
              }
            // =================================================================
                if(e.kategori=='icon'){
                    var greenIcon = L.icon({
                        iconUrl: e.icon,
                        iconSize:     [28, 75], // size of the icon
                        iconAnchor:   [12, 54], // point of the icon which will correspond to marker's location
                        popupAnchor:  [-3, -26] // point from which the popup should open relative to the iconAnchor
                    });
                    if(e.popup=='click'){
                        that.markers['marker'+e.id]=L.marker([e.lat, e.lng],{icon:greenIcon}).bindPopup(`
                        <div class="text-xs">
                            <div class="font-bold uppercase">${e.title}</div>
                            <div class="text-justify">${e.deskripsi}</div>
                            ${e.gambar?`<a href="${e.gambar}" class="mt-2" target="__blank"><img src="${e.gambar}" class="mt-2" style="height:100px;width:100px;" /></a>`:''}
                        </div>
                        `, {maxWidth: 500}).addTo(maps)
                        .addEventListener('click',function(){
                              if(e.event!='false' && e.event){
                                  that.events(e.event)
                                  that.eventshow=true;
                                  that.showMore=false;
                                  let sql=e.query;
                                  that.mores=false;
                                  sdb.collection("t").doc().select(sql).then(res => {
                                      sdb.collection("app_gis_ruangan_gambar",false).doc().select(`select * from app_gis_ruangan_gambar where idroom='${res[0].id}'`).then(res => {
                                        if(res.length>0){
                                          that.mores=true;
                                        }
                                      });
                                      that.eventdata=res;
                                      that.eventkey=Object.keys(res[0])
                                      that.$forceUpdate();
                                  });
                              }
                          })
                    }else{
                        that.markers['marker'+e.id]=L.marker([e.lat, e.lng],{icon:greenIcon}).bindTooltip(`
                        <div class="text-xs">
                            <div class="font-bold uppercase">${e.title}</div>
                            <div class="text-justify">${e.deskripsi}</div>
                            ${e.gambar?`<a href="${e.gambar}" class="mt-2" target="__blank"><img src="${e.gambar}" class="mt-2" style="height:100px;width:100px;" /></a>`:''}
                        </div>
                        `,{ permanent: false, sticky: true }).addTo(maps)
                        .addEventListener('click',function(){
                                   if(e.event!='false' && e.event){
                                      that.events(e.event)
                                      that.eventshow=true;
                                      that.showMore=false;
                                      let sql=e.query;
                                      that.mores=false;
                                      sdb.collection("t").doc().select(sql).then(res => {
                                          sdb.collection("app_gis_ruangan_gambar",false).doc().select(`select * from app_gis_ruangan_gambar where idroom='${res[0].id}'`).then(res => {
                                            if(res.length>0){
                                              that.mores=true;
                                            }
                                          });
                                          that.eventdata=res;
                                          that.eventkey=Object.keys(res[0])
                                          that.$forceUpdate();
                                      });
                                  }
                                })
                    }
            // =================================================================
                }else if(e.kategori=='barcode'){
                   if(this.$route.query.me){
                        if(this.$route.query.me==e.idbarcode){
                    var greenIcon = L.icon({
                        iconUrl: "https://gis.egov.balangankab.go.id/files/me.png",
                        iconSize:     [28, 75], // size of the icon
                        iconAnchor:   [12, 54], // point of the icon which will correspond to marker's location
                        popupAnchor:  [-3, -26] // point from which the popup should open relative to the iconAnchor
                    });
                    if(e.popup=='click'){
                        that.markers['marker'+e.id]=L.marker([e.lat, e.lng],{icon:greenIcon}).bindPopup(`
                        <div class="text-xs">
                            <div class="font-bold uppercase">${e.idbarcode}</div>
                            <div class="text-justify">${e.deskripsi}</div>
                            ${e.gambar?`<a href="${e.gambar}" class="mt-2" target="__blank"><img src="${e.gambar}" class="mt-2" style="height:100px;width:100px;" /></a>`:''}
                        </div>
                        `, {maxWidth: 500}).addTo(maps)
                        .addEventListener('click',function(){
                              if(e.event!='false' && e.event){
                                  that.events(e.event)
                                  that.eventshow=true;
                                  that.showMore=false;
                                  let sql=e.query;
                                  that.mores=false;
                                  sdb.collection("t").doc().select(sql).then(res => {
                                      sdb.collection("app_gis_ruangan_gambar",false).doc().select(`select * from app_gis_ruangan_gambar where idroom='${res[0].id}'`).then(res => {
                                        if(res.length>0){
                                          that.mores=true;
                                        }
                                      });
                                      that.eventdata=res;
                                      that.eventkey=Object.keys(res[0])
                                      that.$forceUpdate();
                                  });
                              }
                          })
                    }else{
                        that.markers['marker'+e.id]=L.marker([e.lat, e.lng],{icon:greenIcon}).bindTooltip(`
                        <div class="text-xs">
                            <div class="font-bold uppercase">${e.title}</div>
                            <div class="text-justify">${e.deskripsi}</div>
                            ${e.gambar?`<a href="${e.gambar}" class="mt-2" target="__blank"><img src="${e.gambar}" class="mt-2" style="height:100px;width:100px;" /></a>`:''}
                        </div>
                        `,{ permanent: false, sticky: true }).addTo(maps)
                        .addEventListener('click',function(){
                                if(e.event!='false' && e.event){
                                  that.events(e.event)
                                  that.eventshow=true;
                                  that.showMore=false;
                                  let sql=e.query;
                                  that.mores=false;
                                  sdb.collection("t").doc().select(sql).then(res => {
                                      sdb.collection("app_gis_ruangan_gambar",false).doc().select(`select * from app_gis_ruangan_gambar where idroom='${res[0].id}'`).then(res => {
                                        if(res.length>0){
                                          that.mores=true;
                                        }
                                      });
                                      that.eventdata=res;
                                      that.eventkey=Object.keys(res[0])
                                      that.$forceUpdate();
                                  });
                              }
                          })
                       }
                     }
                  }
            // =================================================================
                }
                else if(e.kategori=='gambar'){
                  // console.log('gambar',e)
                     var greenIcon = L.icon({
                        iconUrl: e.gambar,
                        iconSize:     [48, 45], // size of the icon
                        iconAnchor:   [12, 54], // point of the icon which will correspond to marker's location
                        popupAnchor:  [-3, -26] // point from which the popup should open relative to the iconAnchor
                    });
                    if(e.popup=='click'){
